*, :before, :after {
  box-sizing: inherit;
  outline: none;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

a {
  all: unset;
  text-decoration: none;
}

textarea {
  resize: none;
}

html {
  box-sizing: border-box;
}

body {
  font-family: DM Sans, sans-serif, Titan One, cursive;
}

img {
  height: auto;
  display: block;
}

a, label, button {
  cursor: pointer;
}

.no-scroll {
  overflow: hidden;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  height: 44px;
